












import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { ImagePreview } from 'vant';

@Component
export default class ImageDetail extends Vue {
  private instance: any = null;
  private viewing: boolean = false;

  @Prop({default: ''}) private photoSrc!: string;
  @Prop({default: {}}) private photos!: any;

  get Images(): string[] {
    if (this.photos.length > 0) {
      return this.photos.map((n: any, i: any) => {
        return n.src;
      });
    }
    return [];
  }

  private showImagePreview(position: any, timer: any) {
    const that = this;
    this.viewing = true;
    if (this.photos.length > 0) {
      const that = this;
      this.$router.push({
        name: 'image',
      });
      this.instance = ImagePreview({
        images: that.Images,
        startPosition: typeof position === 'number' ? position : 0,
        onClose() {
          that.viewing = false;
          if (that.$route.name !== 'bedDetail') {
            history.back();
          }
        },
      });

      if (timer) {
        setTimeout(() => {
          this.instance.close();
        }, timer);
      }
    } else {
      this.$dialog.alert({
        message: '暂无照片，请上传图片',
      });
    }
  }
  private mounted(): void {
    window.addEventListener('popstate', () => {
      if (this.viewing) {
        this.instance && this.instance.close(); // tslint:disable-line
        this.viewing = false;
      }
      return;
    });
  }
}
